<template>
  <!--  -->
  <div class="cart">
    <post-message-component
      @event-post="addTweet"
    ></post-message-component>

    <div>
      <button v-on:click="update" class="btn btn-info-outline">Timeline update</button>
    </div>

    <p v-show="tweets.length===0"><i>Please add some tweets to cart.</i></p>

    <div v-show="tweets.length > 0">
      <table class="checkout-table">
        <tbody>
        <tr v-for="t in tweets | orderBy 'date' -1">
          <td>
            <!--  v-bind:propname (:propname) -->
            <tweet-component v-bind:text="t.text" v-bind:date="t.date"></tweet-component>
          </td>
        </tr>
        </tbody>
      </table>
    </div>

  </div>
</template>

<script>
  import { getTimeline, addTweet } from '../vuex/timeline/actions'
  import TweetComponent from '../components/TweetComponent.vue'
  import PostMessageComponent from '../components/PostMessageComponent.vue'

  export default {
    components: {
      TweetComponent,
      PostMessageComponent
    },
    vuex: {
      // store(App.vue)state
      // getters  pure function 
      getters: {
        //  store.state
        // route  vue-router-sync 
        tweets: ({ timeline, route }) => {
          var id = parseInt(route.params.id)
          console.log('id: ' + id)
          return timeline.all
        }
      },
      actions: {
        getTimeline,
        addTweet
      }
    },
    created () {
      this.getTimeline()
    },
    methods: {
      // this
      update: function () {
        this.getTimeline()
      }
    }
  }
</script>

<style>

.cart {
  width: 600px;
}
.checkout-table {
  width: 100%;
}

.checkout-table th {
  text-align: left;
  padding: 15px 0px;
  border-bottom: 1px solid #aaa;
}

.checkout-table td {
  padding: 8px 0px;
}

.checkout-button {
  float: right;
  width: 120px;
  height: 40px;
  margin-top: 20px;
}

.total td {
  border-top: 1px solid #aaa;
  padding-top: 10px;
}
</style>



